<template>
    <div class="menu-wrapper">
        <template
            v-for="item in $router.options.routes"
            v-if="!item.hidden&&item.children"
        >

            <router-link
                v-if="hasOneShowingChildren(item.children) && !item.children[0].children"
                :to="item.path+'/'+item.children[0].path"
                :key="item.children[0].name"
            >
                <el-menu-item
                    :index="item.path+'/'+item.children[0].path"
                    :class="{'submenu-title-noDropdown':!isNest}"
                >
                    <svg-icon
                        v-if="item.children[0].meta&&item.children[0].meta.icon"
                        :icon-class="item.children[0].meta.icon"
                    ></svg-icon>
                    <span
                        v-if="item.children[0].meta&&item.children[0].meta.title"
                        slot="title"
                    >{{item.children[0].meta.title}}</span>
                </el-menu-item>
            </router-link>

            <el-submenu
                v-else
                :index="item.name||item.path"
                :key="item.name"
            >
                <template slot="title">
                    <svg-icon
                        v-if="item.meta&&item.meta.icon"
                        :icon-class="item.meta.icon"
                    ></svg-icon>
                    <span
                        v-if="item.meta&&item.meta.title"
                        slot="title"
                    >{{item.meta.title}}</span>
                </template>

                <template
                    v-for="child in item.children"
                    v-if="!child.hidden"
                >
                    <sidebar-item
                        :is-nest="true"
                        class="nest-menu"
                        v-if="child.children&&child.children.length>0"
                        :routes="[child]"
                        :key="child.path"
                    ></sidebar-item>

                    <router-link
                        v-else
                        :to="item.path+'/'+child.path"
                        :key="child.name"
                    >
                        <el-menu-item :index="item.path+'/'+child.path">
                            <svg-icon
                                v-if="child.meta&&child.meta.icon"
                                :icon-class="child.meta.icon"
                            ></svg-icon>
                            <span
                                v-if="child.meta&&child.meta.title"
                                slot="title"
                            >{{generateTitle(child.meta.title)}}</span>
                        </el-menu-item>
                    </router-link>
                </template>
            </el-submenu>

        </template>
    </div>
</template>

<script>
// import { generateTitle } from '@/utils/i18n'
export default {
    name: 'SidebarItem',
    props: {
        // routes: {
        //     type: Array
        // },
        isNest: {
            type: Boolean,
            default: false
        }
    },
    methods: {
        generateTitle(title) {
            // const hasKey = this.$te('route.' + title)
            // const translatedTitle = this.$t('route.' + title) // $t :this method from vue-i18n, inject in @/lang/index.js
            // if (hasKey) {
            //     return translatedTitle
            // }
            return title
        },
        hasOneShowingChildren(children) {
            const showingChildren = children.filter(item => {
                return !item.hidden
            })
            if (showingChildren.length === 1) {
                return true
            }
            return false
        }
    },
    created() {
        for (const o in this.$router.options.routes) {
            const obj = this.$router.options.routes[o]
            if (obj.path.indexOf('myiframe') >= 0) {
                obj.children[0].path = 'urlPath?src=https://www.baidu.com'
            }
        }
    }
}
</script>



